<template>
    <div class="service">
        <header>
            <a href="javascript:history.back(-1)">
                <i class="el-icon-arrow-left"></i>
            </a>
            <span>服务中心</span>
        </header>
        <section class="s1">
            <router-link to="">
                <i class="el-icon-service"></i>
                <span>在线客服</span>
            </router-link>
            <router-link to="">
                <i class="el-icon-phone"></i>
                <span>在线客服</span>
            </router-link>
        </section>
        <section class="s2">
            <h4>热门问题</h4>
            <ul>
                <li v-for="c in caption" @click="toQuestionDetail(c)">
                    <span>{{c}}</span>
                    <i class="el-icon-arrow-right"></i>
                </li>
            </ul>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Service",
        data(){
            return{
                caption:[],
            }
        },
        created(){
            this.axios.get("/v3/profile/explain").then(res=>{
                // console.log(res);
                let reg = /Caption/;
                for(let key in res){
                    if(reg.test(key)){
                        this.caption.push(res[key])
                    }
                }
            })
        },
        methods:{
            toQuestionDetail(c){
                this.$router.push({path:"/questionDetail",query:{key:c}});
            }
        }
    }
</script>

<style scoped>
    .service{
        width: 100%;
        background-color: #fff;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .s1{
        margin-top: 1.95rem;
        display: flex;
        justify-content: space-between;
    }
    .s1>a{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 4rem;
        border-bottom: 1px solid #f5f5f5;
    }
    .s1>a:nth-child(1){
        border-right: 1px solid #f5f5f5;
    }
    .s1>a:nth-child(1)>i{
        font-size: 1rem;
        color: rgb(255,123,82);
    }
    .s1>a:nth-child(2)>i{
        font-size: 1.2rem;
        color: rgb(106,194,11);
    }
    .s1 span{
        margin-top: .4rem;
        font-size: .6rem;
        color: #666;
    }
    .s2>h4{
        font-size: .75rem;
        color: #333;
        line-height: 3rem;
        border-bottom: 1px solid #f5f5f5;
        padding-left: .7rem;
    }
    .s2 li{
        padding: 0 .7rem;
        line-height: 2rem;
        border-bottom: 1px solid #f5f5f5;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .s2 span{
        font-size: .6rem;
        color: #666;
    }
    .s2 i{
        font-size: 0.7rem;
        color: rgb(153, 153, 153);
    }
</style>